$gray: #f7f7f7;
/* 932323  b70000*/
* {
  box-sizing: border-box;
}

.unselectable {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.hidden {
  display: none !important;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", Arial;
  //background: $gray;
  background: url("/img/bacground_gray.png");
}


.mobile-icon {
  display: none;
  cursor: pointer;
  width: 45px;
  height: 45px;
}


a {
  text-decoration: none;
}

.main-row {
  padding-bottom: 100px;
}

.header-top {
  height: 30px;
  background: $gray;
  margin-right: auto;
  margin-left: auto;

  .header-menu {
    display: flex;
    height: 100%;
    align-items: center;
    padding-left: 60px;

    a {
      color: #666363;
      display: block;
      font-size: 13px;
      padding: 7px;
      height: 100%;
      text-transform: uppercase;
      transition: 100ms color;
    }

    a:hover {
      color: black;
      font-weight: bold;
    }

  }

  .contacts {
    display: flex;
    font-size: 14px;
    align-items: center;
    color: rgba(0, 0, 0, 0.7);

    div {
      border-left: 1px solid rgba(0, 0, 0, 0.3);
      padding: 0 3px;
    }
  }
}

// 0A58BD
.header {
  background-color: #201569;
  position: sticky;
  top: 0px;
  z-index: 999;

  .wrapper {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    display: flex;
    align-items: center;
    height: 50px;
  }

  .burger-wrapper {
    display: none;
  }

  .logo-wrapper {
    position: relative;
    height: 100%;

    .logo {
      position: absolute;
      top: -5px;
      height: 60px;
      border: 3px solid #201569;
      border-radius: 10px;
    }
  }

  .search-form {
    display: flex;
    align-items: center;
    position: relative;
    height: 100%;

    .search-input {
      height: 35px;
      width: 80%;
      font-size: 18px;
      border-radius: 5px;
      border: 1px solid #093a7a;
      padding: 0 5px;
    }

    .search-input:focus {
      outline: none;
    }


    .search-results {
      display: none;
      position: absolute;
      width: 400px;
      top: 50px;
      background-color: white;
      border: 1px solid #00000033;
      z-index: 99999;
      box-shadow: 0px -1px 9px 3px rgb(0 0 0 / 19%);

      .search-head {
        background: #FBFBFB;
        border-bottom: 1px solid rgba(0, 0, 0, 0.14);
        display: flex;
        width: 100%;
        align-items: center;
        padding: 10px 5px;
        font-size: 18px;

        .title {
          line-height: 30px;
        }

        .close {
          margin-left: auto;
          height: 100%;
          width: 45px;
          line-height: 30px;
          align-items: center;
          color: darkred;
          filter: grayscale(1);
          opacity: 0.5;
          text-align: center;
          transition: 200ms all;
          cursor: pointer;
        }

        .close:hover {
          opacity: 1;
          filter: none;
        }
      }

      .search-body {
        span {
          display: block;
          padding:15px;
          font-size: 18px;
          font-family: "Courier New",sans-serif;
          color:black;
        }

        .search-product {
          display: block;
          padding:0px 8px;
          color: #111;
          text-decoration: none;
          border-bottom: 1px solid rgba(50, 50, 50, 0.05);
          background: transparent;
          z-index: 9;
          height: 36px;
          overflow: hidden;
          line-height: 36px;
        /*
          .animation {
            top: 0px;
            position: absolute;
            height: 100%;
            width: 0px;
            background-color: #111;
            transition: 200ms all;
          } */
        }

        .search-product:hover {
          text-decoration: underline;
        }
      }

      .search-footer {
        border-radius: 6px;
        background: #FBFBFB;
        border-top: 1px solid rgba(0, 0, 0, 0.14);

        a {
          width: 40%;
          text-align: center;
          height: 35px;
          color: white;
          background-color: #111;
          line-height: 35px;
          transition: 200ms all;
          margin-left: auto;
          margin-right: 10px;
          border-radius: 6px;
        }

        a:hover {
          text-decoration: underline;
        }
      }
    }


  }

  .login {
    margin-left: auto;
  }

  .register, .login {
    color: white;
    text-align: center;
    height: 100%;
    padding-top: 15px;
    display: block;

  }

  .register:hover, .login:hover {
    color: black;
    background-color: #FAD42B;
  }

  #cart-button {
    display: flex;
    color: white;
    align-items: center;
    position: relative;
    height: 100%;
    justify-content: center;

    img {
      height: 35px;
      margin-right: 5px;
    }

    .rouble {
      font-weight: bold;
      margin-left: 5px;
    }

    .total-price {

    }
  }

  #cart {
    position: absolute;
    height: 450px;
    width: 350px;
    background-color: white;
    right: 0px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    display: none;
    top: 50px;
    color: black;
    box-shadow: 0px -1px 9px 3px rgb(0 0 0 / 40%);

    .cart-head {
      height: 50px;
      font-size: 16px;
      color: #222;
      font-weight: bold;
      padding: 15px 20px;
      background: #FBFBFB;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);

      span {
        color: #888;
        font-weight: normal;
      }
    }

    .cart-products {
      height: 300px;
      overflow: auto;

      .cart-product {
        position: relative;
        width: 100%;
        display: flex;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);

        .image-wrapper {
          padding: 15px;

          img {
            height: 100px;
          }
        }

        .cart-product-info {
          display: flex;
          flex-direction: column;

          .cart-product-name {
            padding: 15px 5px 5px;
            font-family: "Montserrat", sans-serif;
            display: block;
            flex-grow: 1;
            color: #222;
            font-size: 15px;
          }

          .cart-product-name:hover {
            text-decoration: underline;
            color: black;
          }

          .cart-product-cost {
            align-self: end;
            padding: 12px 10px;
            font-size: 15px;
            line-height: 18px;
            width: 100%;
            color: #888;

            .cart-product-total {
              font-size: 18px;
              font-weight: bold;
              color: black;
            }
          }
        }

        .cart-delete {
          padding-top: 5px;
          padding-left: 15px;
          font-size: 25px;
          top: 0;
          left: 0;
          position: absolute;
          z-index: 9999;
          height: 40px;
          width: 40px;
          opacity: 0.2;
          transition: 200ms all;
        }

        .cart-delete:hover {
          cursor: pointer;
          color: #ac0303;
          opacity: 1;

        }
      }

    }

    .cart-total {
      height: 50px;
      padding: 15px 20px;
      background: #FBFBFB;
      color: #888;
      display: flex;
      align-items: center;

      span {
        color: black;
        font-size: 20px;
        margin-left: 55px;
      }
    }

    .cart-buttons {
      display: flex;
      width: 100%;

      a {
        height: 50px;
        display: inline-block;
        width: 50%;
        text-align: center;
        padding: 15px 0;
        font-size: 15px;
      }
    }

    .order-button {
      background-color: #201569;
      color: white;
      transition: all 200ms;

    }

    .order-button:hover {
      background-color: #332488;
    }

    .cart-button {
      color: #444444;
      background-color: #f5f5f5;

    }
  }
}

.container.main {
  padding: 0;
  min-height: 800px;
}

.sidebar {
  background-color: white;
  padding: 0;
  align-self: flex-start;
  font-family: Arial;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  margin-top: 20px;

  .catalog-name {
    font-size: 24px;
    text-align: center;
    height: 40px;
    padding-top: 5px;
    border-left: 6px solid #090218;
  }

  .catalog-menu {
    position: relative;
    border-left: 6px solid #090218;

    .category {
      border-bottom: 1px solid rgba(0, 0, 0, 0.05);
      position: relative;

      .arrow {
        background: url("/img/icon/arrowLeft.png");
        width: 20px;
        height: 20px;
        background-size: contain;
        position: absolute;
        right: 0px;
        //z-index: 9;
        top: 9px;
        opacity: 0.3;
        transition: opacity 200ms;
      }

      a {
        padding: 10px 5px;
        color: black;
        background-color: white;
        display: block;
        font-size: 15px;
        transition: 200ms all;
        border-left: 5px solid white;
      }

    }

    .submenu .category.selected > a {
      border-left-color: black;
    }

    .category.selected > a {
      background-color: #FAD42B;
      border-left-color: #FAD42B;
    }


    .submenu {
      display: none;
      position: absolute;
      right: -250px;
      top: 0px;
      width: 250px;
      background-color: white;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
      z-index: 9999;
      opacity: 0;
      transition: opacity 200ms;
    }
  }
}


div.breadcrumb {
  padding: 0;
  margin-top: 20px;
  margin-bottom: 15px;
  height: 25px;
  font-size: 14px;
}

ol.breadcrumb {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
  flex-wrap: wrap;

  a {
    display: block;

    span {
      color: black;
      margin-left: 5px;
    }

    span:hover {
      border-bottom: 1px solid black;
    }
  }


  span {
    color: gray;
    margin-left: 5px;
  }

  li a:after {
    content: ' >';
  }
}


#product-detail-card {
  box-shadow: 0 0 10px 4px rgb(0 0 0 / 40%);
  background-color: white;
  border-radius: 15px 15px 0 0;
  margin-left: 10px;

  h1 {
    border-radius: 15px 15px 0 0;
    background-color: #FBFBFB;
    padding: 20px;
    margin: 0px;
    font-family: "Montserrat", sans-serif;
    font-size: 30px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  }

  .main {
    .info {
      padding: 0 10px;

      .stock {
        font-size: 12px;
        width: 94px;
        border-radius: 5px;
        color: #fff;
        background: #39b54a;
        text-align: center;
        line-height: 27px;
        margin-top: 15px;
      }

      .buy {
        padding-top: 20px;

        .main-price {
          padding: 15px 0;
          font-size: 30px;
          font-family: "Montserrat", sans-serif;
          font-weight: bold;
        }
      }

      .add-to-card {
        display: flex;
        align-items: center;

        //0A58BD
        .add2cart {
          height: 55px;
          line-height: 55px;
          width: 160px;
          margin-left: 15px;
          text-align: center;
          font-size: 18px;
          color: white;
          background-color: #201569;
          border-radius: 15px;
          transition: background-color 200ms;
          cursor: pointer;
        }

        .add2cart.disabled {
          filter: grayscale(1);
          opacity: 0.5;
        }


        .add2cart:hover {
          background-color: #2c1ba3;
        }

        .counter {
          display: flex;
          align-items: center;

          .plus, .minus {
            height: 50px;
            width: 50px;
            margin: 0 5px;
            line-height: 50px;
            font-size: 20px;
            text-align: center;
            transition: 200ms;
            cursor: pointer;
          }

          .plus:hover, .minus:hover {
            color: #052dd2;

          }


          .quantity {
            box-shadow: inset 0 2px 5px 0 rgba(0, 0, 0, .2);
            height: 50px;
            width: 50px;
            line-height: 50px;
            text-align: center;
            border-radius: 50%;
            font-size: 18px;
          }
        }
      }
    }
  }


  .gallery {
    .big-carousel {
      padding: 10px;
      width: 400px;
      height: 400px;
      display: flex;
      overflow: hidden;


      img {
        margin-left: auto;
        margin-right: auto;
        height: 400px;
        max-width: 100%;
        object-fit: cover;
      }

      .slick-arrow {
        position: absolute;
        height: 100%;
        width: 100px;
        z-index: 9;
        background: transparent;
        border: 0px;
        color: white;
        font-size: 40px;
        cursor: pointer;
        padding: 0;

        .inner {
          background-color: rgba(0, 0, 0, 0.3);
          width: 65px;
          height: 65px;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          box-sizing: content-box;
          border: 3px solid rgba(0, 0, 0, 0.05);
        }
      }

      .slick-prev {
        right: 0px;
      }
    }

    .slick-arrow:focus {
      outline: none;
    }

    .small-carousel {

      height: 85px;
      margin-top: 15px;
      //display: flex;
      //align-items: center;
      //justify-content: center;

      img {
        height: 90%;
        /*width:80px;*/
        border-radius: 50%;
        margin-left: 10px;
        object-fit: cover;
        border: 4px solid rgb(178 178 178 / 17%);
        padding: 2px;
        cursor: pointer;
      }

      img:focus {
        outline: none;
      }

      img.slick-current {
        box-shadow: #000000a3 1px 4px 11px 1px;
        border-color: transparent;

      }

      .slick-track {
        padding: 12px 0;
        min-height: 80px;
      }
    }
  }

  h2.descHeader {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    background-color: #FBFBFB;
    padding: 15px;
    margin-top: 50px;
  }

  .description {
    padding: 0 25px;
    padding-bottom: 20px;

    .text {
      white-space: pre-wrap;
      line-height: 22px;
      font-size: 16px;

    }

  }
}


.category-products {
  display: flex;
  flex-wrap: wrap;

  .category-item {
    border: 1px solid #2c1ba314;
    /* height: 350px; */
    width: 250px;
    margin: 8px;
    background-color: white;
    box-shadow: 0 2px 6px 0 hsla(0, 0%, 90.2%, .5);
    transition: 100ms;
    display: block;
    color: black;
    padding-top: 8px;

    .image-wrapper {
      width: 100%;
      display: flex;
      justify-content: center;

      img {
        height: 250px;
        max-width: 100%;
        object-fit: cover;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .item-name {
      text-align: center;
      margin-top: 8px;
      font-family: "Montserrat", sans-serif;
      font-size: 15px;
      line-height: 21px;
      padding: 8px;

      a {
        font-family: "Montserrat", sans-serif;
        font-size: 15px;
        line-height: 18px;
        color: #111;
        display: block;

      }

      a:hover {
        text-decoration: underline;
      }
    }

    .prices {
      display: flex;
      width: 100%;
      justify-content: center;
      margin-top: 10px;
      margin-bottom: 25px;

      .new-price, .old-price, .main-price {
        font-size: 18px;
        font-weight: bold;
        line-height: 18px;
        color: #1d1d1d;
      }

      .new-price:after, .old-price:after, .main-price:after {
        content: " ₽";
      }

      .old-price {
        text-decoration: line-through;
      }

    }

    .item-buttons {
      display: flex;

      .counter {
        width: 50%;
        display: flex;

        .plus, .minus {
          display: inline-block;
          width: 38%;
          text-align: center;
          height: 40px;
          line-height: 40px;
          font-size: 25px;
        }

        .plus:hover, .minus:hover {
          cursor: pointer;
        }

        .quantity {
          text-align: center;
          display: inline-block;
          width: 20%;
          height: 40px;
          line-height: 40px;
        }
      }

      .add2cart {
        width: 50%;
        height: 40px;
        line-height: 40px;
        text-align: center;
        background-color: #201569;
        color: white;
        cursor: pointer;
        transition: background-color 200ms, text-shadow 200ms;
      }

      .disabled {
        filter: grayscale(1);
        opacity: 0.5;
        cursor: not-allowed;
      }

      .add2cart:hover {
        background-color: #2c1ba3;
        text-shadow: 0 0 1px white;
      }
    }

  }

  .category-item:hover {
    box-shadow: 0 0 20px 0 hsla(0, 0%, 62.7%, .4);
  }

}

#catalog-categories {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  .catalog-category {
    display: flex;
    flex-direction: column;
    background-color: white;
    margin: 8px;
    border: 2px solid rgba(0, 0, 0, 0.09);
    width: 250px;
    align-items: center;

    img {
      height: 250px;
      object-fit: cover;

    }

    a {
      width: 100%;
      flex-grow: 1;
      background-color: #1d0426;
      height: 100%;
      font-family: "Montserrat", sans-serif;
      line-height: 18px;
      color: white;
      display: block;
      text-align: center;
      font-size: 18px;
      padding: 10px 5px;
    }

    a:hover {
      text-decoration: underline;
      text-shadow: 0 0 1px white;
    }

  }
}

