    display: block;
    font-size: 16px;
    padding: 2px 5px;
    margin: 5px 0;
    height: 35px;
    width: 350px;
    background-color: #f7f7f7;;
    //border:1px solid rgba(20,20,20,0.3);
    border: none;
    color: black;

  }

  input.submit {
    margin-left: auto;
    margin-top: 10px;
    margin-right: 20px;
    width: 250px;
    height: 50px;
    background-color: #0A58BD;
    color: white;
    border-radius: 5px;
