      padding: 0 20px;
      position: relative;
      width: 100%;
      display: flex;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);

      .image-wrapper {
        padding: 5px;

        img {
          height: 150px;
        }
      }

      .cart-product-info {
        display: flex;
        flex-direction: column;
        padding: 15px;

        .cart-product-name {
          font-family: "Montserrat", sans-serif;
          display: block;
          color: #222;
          font-size: 20px;
        }

        .cart-product-name:hover {
          text-decoration: underline;
          color: black;
        }

        .cart-product-cost {
          align-self: end;
          padding: 12px 10px;
          font-size: 16px;
          line-height: 18px;
          width: 100%;
          color: #888;

          span {
            font-size: 18px;
            font-weight: bold;
            color: black;
          }
        }
      }
    }

    .cart-total {
      font-size: 24px;
      text-align: right;
      padding: 8px 8px 8px 50px;
    }
  }
}

.order-success {
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    height: 75px;

  }

  .message {
    font-size: 22px;
    margin-left: 25px;
  }

  p {
    margin: 5px 0;
    font-size: 17px;
  }
}

footer {
  background-color: black;
  padding-bottom: 15px;

  .sitemap {
    padding: 20px 0;

    span {
      font-size: 20px;
      display: block;
      color: white;
      margin-bottom: 15px;
    }

    a {
      display: block;
      color: white;
      font-size: 17px;
      line-height: 22px;
    }

    a:hover {
      text-decoration: underline;
    }
  }

  .contacts {
    color: white;
    padding: 20px 0;

    .whatsapp {
